import { CanvasCore } from './CanvasCore';
import { Core } from './Core/Core';
export class AppCanvasTemplate {
    canvasCore: CanvasCore;
    core: Core;
    static Init(id: string) {
        const n = new AppCanvasTemplate(id);
        n.Dummy();
    }
    constructor(id: string = 'SandBox') {
        this.canvasCore = new CanvasCore(id);
        this.core = new Core(this.canvasCore);
        this.BindEvent();
        this.BindRenderer();
    }
    Dummy() {
        //
    }
    BindEvent() {
        this.canvasCore.MouseDown = (e: MouseEvent) => this.core.MouseDown(e);
        this.canvasCore.MouseMove = (e: MouseEvent) => this.core.MouseMove(e);
        this.canvasCore.MouseUp = (e: MouseEvent) => this.core.MouseUp(e);
    }
    BindRenderer() {
        this.canvasCore.Render = (ctx: CanvasRenderingContext2D) => this.core.Render(ctx);
    }
}


export class Solution {
    public divHost: HTMLElement;

    constructor(id: string, index = 0) {
        this.divHost = document.getElementById(id);

        const div = document.createElement('div');
        div.id ='ngl3d';
        div.style.width = '100%';
        div.style.height = '100%';
        this.divHost.appendChild(div);

        AppCanvasTemplate.Init(div.id);
    }
    public destroy() {
        try {
            while (this.divHost !== undefined && this.divHost.lastElementChild !== undefined) {
                this.divHost.removeChild(this.divHost.lastElementChild);
            }
        } catch (error) { console.debug(error) }
    }
}